@media (max-width: 360px) {
  .box {
    padding: 10px 5px 10px 10px;
    .box-money {
      margin-left: 5px;
    }
  }

  .btn {
    height: 50px;
    width: 137px;
    &.btn-start {
    }
    &.btn-share {
      width: 130px;
      height: 50px;
    }
    &.btn-money {
      width: 130px;
      height: 50px;
    }
  }

  .modal {
    .modal-footer {
      padding: 10px 0;
      .btn {
        height: 40px;
        width: 110px;
      }
    }
  }



  .btn.btn-rule, .btn.btn-time {
    width: 130px;
    height: 30px;
    margin: 0px 0 0;
  }
  #roleState{
    p{
      font-size: 11px;
    }
  }

  .input-box.btn-reset{
    font-size: 1.2em;
  }


  .input-box {
    padding: 8px 30px 10px 20px;
    input {
      font-size: 1.8em;
    }
  }
}
