.logo, .subject {
  width: 60%;
  margin: 18px auto 0;
  display: block;
}

.subject {
  width: 70%;
  margin-top: 20px;
}

.home-btn {
  text-align: center;
}

.home-fot {
  margin: 20px 2%;
}


.btn {
  background: url('../res/btn-start.png') no-repeat;
  background-size: 100%;
  position: relative;
  height: 56px;
  width: 154px;
  display: inline-block;
  &.btn-start {
    margin: 30px 0 15px;
  }
  &.btn-share {
    width: 156px;
    height: 60px;
    background-image: url(../res/btn-share.png);
  }
  &.btn-money {
    width: 156px;
    height: 60px;
    background-image: url(../res/btn-today-money.png);
  }
  &.btn-time {
    width: 200px;
    height: 50px;
    background-image: url(../res/label-game-of-time.png);
    margin: 15px 0 0;
  }
  &.btn-rule {
    width: 200px;
    height: 50px;
    margin: 15px 0 0;
    background-image: url(../res/label-game-of-rule.png);
  }
  &.btn-close {
    width: 35px;
    height: 35px;
    position: absolute;
    right: 0px;
    top: 0px;
    background-image: url(../res/cha.png);
  }
  &.btn-by-me {
    margin-top: 10px;
    width: 100%;
    height: 90px;
    background-image: url(../res/btn-by-me3.png);
    &.not-play{
      background-image: url(../res/btn-by-me2.png);
    }
  }
}

.box-btn {
  background: url('../res/btn-convert.png') no-repeat;
  background-size: 100% 100%;
  position: relative;
  height: 55px;
  width: 125px;
  display: inline-block;
  &.use {
    background-image: url("../res/btn-convert-use.png");
  }
}

#shareState {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: 0.9;
  }
  img {
    position: absolute;
    top: 10px;
    right: 20px;
    width: 60%;
  }
}

#roleState {
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: @mainColor;
  }
}

.friend {
  text-align: left;
  background-color: #F3DD9F;
  border-radius: 10px;
  padding: 10px;
  p {
    margin: 0;
    font-size: 1.1em;
  }
  .picture {
    float: left;
    width: 50px;
    img {
      width: 50px;
    }
  }
  .friend-body {
    margin-left: 60px;
  }
  .username {
    font-size: 1.3em;
    margin-bottom: 6px;
  }
  .play-count {
    margin-top: 10px;
    font-size: 1.4em;
  }
}

.box {
  .clearfix();
  background: #F0DDA2;
  font-size: 1.5em;
  padding: 20px 10px 20px 20px;
  border-radius: 20px;
  line-height: 55px;
  margin-top: 15px;
  .box-money {
    margin-left: 20px;
    color: #DE6B66;
  }
  .box-title {
    a {
      display: block;
      font-size: 12px;
      margin: 0;
      line-height: 0;
      position: relative;
      top: -5px;
    }
  }
}

.money-role {
  padding-left: 25px;
  font-size: 13px;
}

.font-size-1-2 {
  font-size: 1.2em;
}

.margin-top-30 {
  margin-top: 30px;
}

.ti2 {
  color: #96b377;
}

.hd-1 {
  font-size: 1.2em;
  text-align: center;
  padding: 60px 0 30px 0;
}

.input-box {
  background: url('../res/dianhuahaoma.png') no-repeat;
  background-size: 100% 100%;
  padding: 8px 40px 10px 30px;
  position: relative;
  height: 65px;
  .input-reset-btn {
    background: url('../res/btn-delete.png') no-repeat;
    background-size: 100% 100%;
    width: 35px;
    height: 35px;
    position: absolute;
    right: 10px;
    top: 17px;
    display: none;
  }
  input {
    outline: none;
    display: block;
    height: 100%;
    width: 100%;
    background: transparent;
    border: none;
    font-size: 2em;
  }
  button {
    outline: none;
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
  }

  &.gray {
    background-image: url(../res/2weima.png);
  }
  &.next {
    background-image: url(../res/btn-next.png);
  }
  &.btn-reset {
    display: block;
    color: #000;
    padding: 0 5px;
    font-size: 1.4em;
    text-align: center;
    line-height: 62px;
    background-image: url(../res/btn-reset.png);
  }
  &.money {
    background-image: url(../res/btn-money2.png);
  }
  &.download {
    display: block;
    background-image: url(../res/btn-download.png);
  }
  &.download-share {
    display: block;
    background-image: url(../res/btn-download-share.png);
  }
  &.captcha {
    padding: 8px;
    background: none;
    img {
      width: 100%;
      height: 49px;
    }
  }
  &.input-captcha{
    background-image: url(../res/input-bg.png);
  }
  &.error{
    background-image: url(../res/input-error.png);
  }
}

.clause {
  font-size: 1.2em;
  line-height: 40px;
  padding: 20px 0;

  &.not {
    .input-checkbox {
      background-image: url('../res/btn-read-not.png');
    }
  }
  .input-checkbox {
    background: url('../res/btn-read.png') no-repeat;
    background-size: 100% 100%;
    position: relative;
    height: 40px;
    width: 40px;
    margin-right: 10px;
    display: inline-block;
    float: left;
  }
}

.ok {
  background: url('../res/ok.png') no-repeat;
  background-size: 100% 100%;
  position: relative;
  height: 135px;
  width: 150px;
  margin: 50px auto 30px;

}

.hd-2 {
  margin: 30px 0;
}

.top {
  margin: 0 5% 10px;
  text-align: center;
  .top-img {
    position: relative;
    height: 120px;
    width: 120px;
    display: inline-block;
  }
  .top-img2{
    width: 136px;
  }
}

.list-title {
  position: relative;
  text-align: center;
  margin: 20px 0;
  &:before {
    content: '';
    height: 1px;
    background: #000;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
  }
  .list-title-inner {
    position: relative;
    background: @mainColor;
    padding: 0 20px;
    font-style: normal;
  }
}

.user-list {
  list-style: none;
  padding: 0;
}

.list-null {
  text-align: center;
  color: #bcbcbc;
  margin-top: 30px;
}

.user-item {
  .clearfix();
  font-size: 1.2em;
  margin-bottom: 20px;
  .picture {
    img {
      width: 60px;
    }
  }
  .username {
    padding: 0px 10px;
  }
  .help-money {
    padding: 0;
  }
}

.modal {
  .modal-dialog {
    z-index: 1050;
    margin: 40% 10%;
  }
  .modal-backdrop.in {
    opacity: 0.9;
  }
  .modal-content {
    background-color: #F7E6B3;
    border-radius: 20px;
  }
  .modal-footer {
    border: none;
    text-align: center;
    .btn {
      background: url('../res/btn-modal-ok.png') no-repeat;
      background-size: 100% 100%;
      position: relative;
      height: 55px;
      width: 125px;
      display: inline-block;
      border: none;
      &.modal-close {
        background-image: url("../res/btn-modal-no.png");
      }
    }
  }
  .modal-body {
    font-size: 1.2em;
  }
}

.tips {
  display: none;
  position: fixed;
  left:0;
  top: 50%;
  width: 100%;
  font-size: 1.1em;
  text-align: center;
  .tips-warp{
    position: relative;
    display: inline-block;
    &:before {
      content: '';
      border-radius: 5px;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: #000;
      opacity: 0.7;
    }
    span {
      display: inline-block;
      padding:5px 10px;
      position: relative;
      z-index: 1;
      color: #FFF;
    }
  }
}


.inscribe{
  position: absolute;
  bottom: 0;
  left: 10px;
}


.friend-play-count{
  color: #f00;
}